<template>
  <div class="meng">
    <div class="wutai">
      <div class="offense item">
    {{teamData.offense}}

      </div>
     <div class="pk">
       <!-- vs -->
       <img class="pkImg" :src="pageSrc.goImg" >
     </div>
      <div class="defender item">
    
    {{teamData.defender}}
      </div>

    </div>
  </div>
</template>

<script>
import vsImg from "@/assets/img/vs.png";
import goImg from "@/assets/img/go.png";
export default {
  name: "fight",
  data() {
    return {
      pageSrc: {
        vsImg,
        goImg
      }
    };
  },
  methods: {},
  props: ["teamData"]
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.meng {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.911);
  top: 0;
  left: 0;
  z-index: 1;
  .wutai {
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 50%;
    transform: translateY(-50%);
    .pk {
      text-align: center;
      width: 30%;
      .pkImg {
        width: 100%;
        margin-left: -10%;
        transform: scale(2.2);
      }
    }
    .item {
      font-size: 5rem;
      position: relative;
      // text-shadow: 0 0 20px rgb(212, 17, 17);
      &::before {
        width: 130%;
        height: 3px;
        content: "";
        position: absolute;
      }
      &.offense {
        &::before {
          background-color: #fff;
          top: -10%;
          left: -20%;
        }
        text-shadow: 0 0 20px rgb(212, 17, 17);
      }
      &.defender {
        text-shadow: 0 0 20px rgb(17, 140, 212);
        &::before {
          background-color: rgb(207, 19, 19);
          bottom: -50%;
          left: 10%;
        }
      }
    }
  }
}
@keyframes identifier {
  
}
</style>



